.goods {
  display: flex;
  justify-content: center;
  width: 100%;

  &__item-container {   //  left container
    display: block;
    width: 553px;
    height: 690px;
    float: left;
    margin-right: 55px;
    padding-top: 52px;
  }
  &__item-thumbnail {
    display: block;
    float: left;
    width: 109px;
    height: 148px;
    margin-bottom: 12px;
    clear: both;
  }

  &__item {
    float: left;
    position: relative;
    width: 421px;
    top: -160px;
    left: 16px;
    z-index: 2;
  }
  &__zoom-item {
    display: block;
    position: absolute;
    width: 421px;
    height: 650px;
    z-index: 1;
    left: 512px;
  }
  &__description {  //right container
    display: block;
    float: left;
    width: 492px;
    height: 650px;
    margin-left: 55px;
    padding-top: 30px;
    text-align: center;
  }
  &__par {
    display: block;
    color: #9fcede;
    margin-bottom: 20px;
    font-family: Georgia, sans-serif;
    font-size: 12px;
    font-weight: 400;
    &:before {
      display: block;
      width: 12px;
      height: 12px;
      padding-top: 19px;
      margin-left: 150px;
      content: url("./../assets/man-icon.svg");
    }
  }
  &__item-name {
    display: block;
    margin: 0 auto;
    width: 400px;
    color: #303032;
    font-family: "PT Sans - Narrow Bold", sans-serif;
    font-size: 24px;
    line-height: 30px;
    text-transform: uppercase;
  }
  &__price {
    display: block;
    margin: 58px 0;
    color: #303032;
    font-family: Georgia, sans-serif;
    font-size: 22px;
    font-weight: 400;
  }
  &__description-banner {
    color: #333743;
    font-family: "PT Sans - Narrow Bold", sans-serif;
    font-size: 13px;
    font-weight: 700;
    line-height: 25px;
    border-bottom: 1px solid #dbdee5;

  }
  &__specification {
    margin-top: 20px;
    margin-bottom: 50px;
    color: #9297a3;
    font-family: Georgia, sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    white-space: pre;
  }
  &__change-item-color {
    text-align: left;
    margin-bottom: 70px;
    p {
      display: block;
      width: 145px;
      margin-bottom: 3px;
      color: #333743;
      font-family: "PT Sans - Narrow Bold", sans-serif;
      font-weight: 700;
      font-size: 13px;
      line-height: 25px;
      border-bottom: 1px solid #dbdee5;
    }
  }
  &__color-choice {
    display: block;
    margin: 5px 11px 0 -20px;
    color: #9297a3;
    font-family: Georgia, sans-serif;
    font-size: 13px;
    font-weight: 400;
    line-height: 25px;
    outline: none;
    &:first-child {
      float: left;
    }
    /*    input-animation    */
    input {
      display: none;
    }
    i {
      display: inline-block;
      width: 15px;
      height: 16px;
      border: 1px solid #9297a3;
      position: relative;
      background-color: #ffffff;
    }
    input[type="radio"] + i {
      border-radius: 50%;
    }
    i:before {
      content: "\2714";
      width: 7px;
      height: 6px;
      top: -6px;
      left: -1px;
      color: white;
      padding-left: 2px;
      padding-bottom: 2px;
      vertical-align: middle;
      position: absolute;
      border-radius: 50%;
      transform: scale(0);
      transition: 0.4s;

    }
    input[type="radio"] + i:before {
      border-radius: 50%;
    }
    input[type="radio"]:checked + i:before {
      content: "\2714";
      transform: scale(1);
    }
    input[type="radio"]:checked + i {
      background-color: #333743;
    }
  }
  &__color-value {
    color: #9297a3;
    font-family: Georgia, sans-serif;
    font-size: 13px;
    font-weight: 400;
    line-height: 25px;
    h4 {
      display: block;
      margin: 5px 0;
      &:before {
        content: "â– ";
        color: #9fcede;
        width: 10px;
        height: 10px;
        zoom: 2;
        padding-right: 9px;
      }
    }
    &:last-child {
      h4:before {
        color: #333743;
      }
    }
  }
  &__buy-button {
    display: block;
    width: 489px;
    height: 49px;
    margin: 0 auto;
    border: none;
    outline: none;
    border-radius: 2px;
    background-color: #afa394;
    color: #ffffff;
    font-family: Georgia, sans-serif;
    font-size: 13px;
    font-weight: 400;
    line-height: 25px;
    &:hover {
      background-color: #7ec42e;
      transition: 0.1s;
    }
  }
  @media all and (max-width: 1640px) {
    &__zoom-item {
      left: 342px;
    }
  }
  @media all and (max-width: 1400px) {
    &__zoom-item {
      left: 220px;
    }
  }
  @media all and (max-width: 1300px) {
    &__zoom-item {
      left: 180px;
    }
  }
  @media all and (max-width: 1250px) {
    flex-direction: column;
    align-items: center;
    &__item-container {
      height: 650px;
      margin-bottom: 30px;
    }
    &__description {
      margin-left: 0;
    }
    &__zoom-item {
      left: 50%;
      margin-left: -180px;
    }
    &__change-item-color {
      margin-bottom: 40px;
    }
  }
  @media all and (max-width: 625px) {
    &__item-container {
      width: 300px;
      height: 400px;
      margin: 0 auto;
    }
    &__item-thumbnail {
      width: 50px;
      height: auto;
      /* height: 148px; */
    }
    &__item {
      width: 250px;
      height: 386px;
      top: -80px;
    }
    &__zoom-item {
      width: 250px;
      height: 386px;
      margin-left: -90px;
    }
    &__description {
      width: 100%;
    }
    &__par {
      margin-top: 40px;
      &:before {
        display: unset;
        margin-left: 0;
        margin-right: 5px;
      }
    }
    &__item-name {
      width: 300px;
      padding: 0 7px;
      font-size: 17px;
    }
    &__price {
      margin: 30px 0;
    }
    &__description-banner {
      margin: 0 auto;
      padding-bottom: 10px;
    }
    &__specification {
      display: block;
      white-space: normal;
      margin: 0 auto;
      padding: 5px 10px;
    }
    &__change-item-color {
      margin-left: 30px;
      margin-bottom: 30px;
    }
    &__color-choice {
      margin-left: 15px;
    }

    &__buy-button {
      width: 200px;
    }
  }
  @media all and (max-width: 360px) {
    &__item {
      width: 200px;
      height: 336px;
      top: -80px;
    }
    &__zoom-item {
      width: 200px;
      height: 336px;
      margin-left: -90px;
    }
  }
}
